}

.toolTip{
    color: red;
}

.image-container {
    backdrop-filter: blur(10px);
    background-color: #000000a1;
    position: absolute;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    z-index: 5;
}

#rotatingImage {
    position: absolute;
    left: 42%;
    top: 25%;
    animation: rotateAndScale 2s infinite ease-in-out;
    width: 15%;
}

@keyframes slideIn {
    0% {
        transform: translateX(-100%);
        /* Start position (off-screen to the left) */
    }

    100% {
        transform: translateX(0%);
        /* End position (center of the screen) */
    }
}

@keyframes slideOut {
    0% {
        transform: translateX(75%);
        /* Start position (off-screen to the left) */
    }

    100% {
        transform: translateX(0%);
        /* End position (center of the screen) */
    }
}

.slideOut {
    animation: slideOut 1s ease-in-out;
}

.slideIn {
    animation: slideIn 1s ease-in-out;
}

@keyframes rotateAndScale {
    0% {
        transform: rotate(0deg) scale(1);
    }

    50% {
        transform: rotate(200deg) scale(0.3);
        filter: drop-shadow(0px 0px 10px #03989e);
        filter: blur(4);
        filter: invert(1);
    }

    100% {
        transform: rotate(360deg) scale(1);
        filter: drop-shadow(0px 0px 0px black);
    }
}

@keyframes reveal {
    0% {
        transform: rotate(270deg) scale(0.2);
        filter: blur(4);
    }

    100% {
        transform: rotate(360deg) scale(1);
        /* filter: blur(0); */
    }
}

.reveal {
    animation: reveal 1s ease-in-out;
}

@keyframes close {

    0% {
        transform: rotate(360deg) scale(1);
        /* filter: blur(0); */
    }

    100% {
        transform: rotate(270deg) scale(0.2);
        filter: blur(4);
    }
}

.close {
    animation: close 1s ease-in-out;
}

